.header {
  margin-top: 20px;
}

.logo {
  display: flex;
  align-items: center;
  color: #e5697c;
}

.maple {
  width: 3.5rem;
  height: 3.5rem;
}

.header-right {
  display: flex;
  align-items: center;
}

.search {
  width: 1.5rem;
  height: 1.5rem;
}
.search:hover {
  cursor: pointer;
}

.notifications {
  position: relative;
  width: 1.5rem;
  height: 1.5rem;
}

.bell {
  position: absolute;
  width: 100%;
  height: 100%;
}

.burger {
  width: 1.8rem;
  height: 1.5rem;
  fill: blue;
}
.burger:hover {
  cursor: pointer;
}

.avatar {
  height: 3rem;
  margin-right: 0.5rem;
}

.arrow-down {
  height: 0.5rem;
}

.mr {
  margin-right: 1rem;
}
